.p-image--bordered {
  border: 1px solid #d9d9d9;
}

.p-image--shadowed {
  box-shadow: 0 1px 5px 1px #d9d9d933;
}

.p-link--soft {
  color: #111;
}

.p-link--soft:visited {
  color: #111;
  text-decoration: none;
}

.p-link--soft:hover {
  color: #06c;
}

.p-link--soft.is-selected {
  font-weight: 400;
}

.p-link--inverted {
  color: #f7f7f7;
  font-weight: 400;
}

.p-link--inverted:hover {
  color: #f7f7f7;
}

.p-link--inverted:visited {
  color: #dedede;
}

.p-link--skip {
  color: #06c;
  display: block;
  position: absolute;
  top: -999px;
  left: -999px;
}

.p-link--skip:focus {
  z-index: 999999;
  padding: .25rem;
  position: relative;
  top: 0;
  left: 0;
}

.p-link--skip:focus:focus, .p-link--skip:focus:focus-visible {
  outline-offset: -.1875rem;
  outline: .1875rem solid #2e96ff;
}

.p-link--skip:focus:focus:not(:focus-visible) {
  outline-offset: 0;
  outline: 0;
}

.p-top {
  align-items: center;
  display: flex;
}

.p-top:before {
  content: "";
  border-bottom: 1px solid #00000026;
  flex-grow: 1;
  margin: 1.5rem 0 calc(1.5rem - 1px);
}

